const DEFAULT_TYPE_ACTIVITIES = [
  { id: "BOTH", name: "ทั่วไป" },
  { id: "PHOTO", name: "ใช้รูปภาพ" },
  { id: "PDF", name: "ใช้ PDF" },
];

// Get all type activities (compat)
export const getAllTypeActivities = async () => {
  return DEFAULT_TYPE_ACTIVITIES;
};

// Get type activity by ID
export const getTypeActivityById = async (id) => {
  return DEFAULT_TYPE_ACTIVITIES.find((item) => item.id === id) || null;
};

